:root {
  --primary-color: #0f0404;
  --secondary-color: #00ffe1;
  --tetiary-color: #fffdd0;
}

body {
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

main {
  width: 360px;
  min-height: auto;
  background-color: var(--secondary-color);
}

#input-items, #add-btn {
  border: none;
  outline: none;
  padding: 10px;
}

main h1, h3, .input-wrapper {
  text-align: center;
}

li {
  background-color: #fff;
  border-radius: 5px;
  justify-content: space-between;
  margin: 10px;
  padding: 7px;
  line-height: 1.5rem;
  display: flex;
  box-shadow: 5px 5px 5px #0000001a, -5px -5px 5px #0000001a;
}

img {
  width: 20px;
  height: 20px;
  color: #000;
}

.done {
  text-decoration: line-through;
}

